import React, { useState, useEffect } from 'react';
import CardList from '../components/CardList';
import SearchBox from '../components/SearchBox';
import Scroll from '../components/Scroll';
import './App.css';

const App = () => {
  
  const [robots, setRobots] = useState([]);
  const [searchfield, setSearchfield] = useState('');

  //useEffect runs after every render, so the App keeps fetching the robots from API
  // To avoid that don't set state inside useEffect
  // You can use [] which is shortcut for componentDidMount, 
  // that tells React to run useEffect only if [] changes.
  useEffect(() => {
  	fetch('https://jsonplaceholder.typicode.com/users')
      .then(response=> response.json())
      .then(users => {setRobots(users)});
  }, [])

  const onInputChange = (event) => {
    setSearchfield(event.target.value)
  }

    const filteredRobots = robots.filter(robot =>{
      return robot.name.toLowerCase().includes(searchfield.toLowerCase());
    })
    return !robots.length ?
      <h1>Loading</h1> :
      (
        <div className='tc'>
          {/*<h1 className='f1'>RoboFriends</h1>*/}
          {<SearchBox onInputChange={onInputChange}/>}
          <Scroll>
            <CardList robots={filteredRobots} />
          </Scroll>
        </div>
      );
}

export default App;


